:root {
    --bg: #171717;
    --dark: #050505;
    --hover: #1a1a1a;

    --text: #ffffff;
    --muted: #9b9b9b;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: monospace;
    background: var(--bg);
    color: var(--text);
}

h1 {
    margin: 0;
    color: var(--text);
}

header {
    background: var(--dark);
    padding: 24px;
    display: flex;
    align-items: center;
    gap: 20px;
    font-size: 12px;
    z-index: 1000;
}

a {
    color: var(--muted);
    font-size: 16px;
    text-decoration: underline;
    width: fit-content;
    transition: transform 0.2s, color 0.2s;
}

a:hover {
    transform: scale(1.05);
    color: var(--text);
}

button {
    background: transparent;
    border: none;
    color: var(--text);
    padding: 10px;
    margin-right: 50px;
    cursor: pointer;
    transition: background 0.2s;
}

button:hover {
    background: var(--bg);
}

.layout {
    display: flex;
    min-height: calc(100vh - 111px);
    overflow-x: hidden;
}

aside {
    width: 220px;
    background: var(--dark);
    transition: transform 0.5s ease-in;
    z-index: 900;
}

aside.shift {
    transform: translateX(calc(-1 * 220px));
}

.aside-btn {
    width: 100%;
    font-size: 24px;
    color: #bebebe;
    transition: background 0.2s, color 0.2s;
}

.aside-btn:hover {
    background: var(--hover);
    color: var(--text);
}

main {
    flex: 1;
    padding: 40px;
    z-index: 1;
    transition: transform 0.5s ease-in;
}

main.shift {
    transform: translateX(calc(-1 * 220px));
}

.line {
    max-width: 60%;
    height: 2px;
    background: #c7c7c7;
    border-radius: 20px;
    margin-bottom: 40px;
}

.dev-section,
.skills-section,
.social__media-section {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 50px;
}

.dev-card,
.skill-card,
.social__media-card {
    background: var(--dark);
    padding: 20px;
    border-radius: 15px;
}

.dev-card {
    max-width: 330px;
    text-align: center;
}

.progress__value {
    color: var(--dark);
}

.skill-card {
    max-width: 330px;
}

.social__media-card {
    width: fit-content;
    max-width: 330px;
}

.progress__bar-inner {
    height: 15px;
    background: #2b2b2b;
    border-radius: 50px;
    overflow: hidden;
}

.progress__bar {
    height: 100%;
    width: 0;
    border-radius: 50px;
    background: linear-gradient(90deg, #fff, #5c5c5c);
    transition: width 0.6s ease;
}

@media (max-width: 1024px) {
    main {
        padding: 30px;
    }

    .skill-card {
        max-width: 45%;
    }
}

@media (max-width: 768px) {

    header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 111px;
    }

    main {
        margin-top: 111px;
        padding: 24px;
    }

    aside {
        position: fixed;
        top: 111px;
        left: 0;
        height: calc(100vh - 111px);
    }

    .skill-card {
        max-width: 100%;
    }
}

@media (max-width: 480px) {

    header {
        padding: 16px;
    }

    main {
        padding: 16px;
    }

    .aside-btn {
        font-size: 20px;
    }

    a {
        font-size: 14px;
    }
}